import React, { useState } from 'react';
import Link from 'next/link';
import { MapPin, Star, Heart } from 'lucide-react';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { ImageWithFallback } from '@/components/figma/ImageWithFallback';

interface PackageCardProps {
  id?: string | number;
  image: string;
  title: string;
  location: string;
  rating: number;
  reviews: number;
  description: string;
  badges: ReadonlyArray<'halal' | 'no-alcohol' | 'prayer-room' | 'family-friendly' | 'women-only'> | Array<'halal' | 'no-alcohol' | 'prayer-room' | 'family-friendly' | 'women-only'>;
  price: number;
  priceLabel: string;
  layout?: 'horizontal' | 'vertical';
}

export function PackageCard({
  id,
  image,
  title,
  location,
  rating,
  reviews,
  description,
  badges,
  price,
  priceLabel,
  layout = 'horizontal'
}: PackageCardProps) {
  const [isWishlisted, setIsWishlisted] = useState(false);

  // Format rating to 1 decimal place
  const formattedRating = rating ? Number(rating).toFixed(1) : '0.0';
  const hasValidRating = rating && rating > 0;
  const hasReviews = reviews && reviews > 0;

  // Vertical layout for grid view
  if (layout === 'vertical') {
    return (
      <Link href={`/package/${id || 1}`} className="group block h-full">
        <div className="h-full bg-white rounded-2xl overflow-hidden shadow-[0_2px_8px_rgba(0,0,0,0.04)] hover:shadow-[0_8px_30px_rgba(0,0,0,0.12)] transition-all duration-500 border border-neutral-100 hover:border-emerald-200 flex flex-col group-hover:-translate-y-1">
          {/* Image with gradient overlay */}
          <div className="relative aspect-[4/3] overflow-hidden bg-gradient-to-br from-neutral-100 to-neutral-50 flex-shrink-0">
            <ImageWithFallback
              src={image}
              alt={title}
              className="absolute inset-0 w-full h-full object-cover group-hover:scale-110 transition-transform duration-700 ease-out"
            />
            {/* Subtle gradient overlay for depth */}
            <div className="absolute inset-0 bg-gradient-to-t from-black/5 via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-500" />

            <button
              onClick={(e) => {
                e.preventDefault();
                setIsWishlisted(!isWishlisted);
              }}
              className="absolute top-4 right-4 w-10 h-10 bg-white/90 backdrop-blur-md rounded-full flex items-center justify-center shadow-lg hover:bg-white hover:scale-110 transition-all duration-300 z-10"
            >
              <Heart className={`w-[18px] h-[18px] transition-all duration-300 ${isWishlisted ? 'fill-red-500 text-red-500 scale-110' : 'text-neutral-700'}`} />
            </button>
          </div>

          {/* Content */}
          <div className="p-5 flex flex-col flex-1 min-h-0">
            {/* Title & Location */}
            <div className="mb-3">
              <h3 className="text-lg font-bold text-neutral-900 line-clamp-1 mb-2 group-hover:text-emerald-600 transition-colors duration-300">
                {title}
              </h3>
              <div className="flex items-center gap-2 text-neutral-600">
                <MapPin className="w-4 h-4 flex-shrink-0 text-emerald-500" />
                <span className="text-sm font-medium line-clamp-1">{location}</span>
              </div>
            </div>

            {/* Rating */}
            <div className="mb-3 h-7 flex items-center">
              {hasValidRating ? (
                <div className="flex items-center gap-2">
                  <div className="flex items-center gap-1.5 px-2.5 py-1 bg-gradient-to-r from-yellow-50 to-amber-50 rounded-lg border border-yellow-100">
                    <Star className="w-4 h-4 fill-yellow-400 text-yellow-400" />
                    <span className="text-sm font-bold text-neutral-900">{formattedRating}</span>
                  </div>
                  {hasReviews && (
                    <span className="text-xs text-neutral-500 font-medium">({reviews.toLocaleString()} reviews)</span>
                  )}
                </div>
              ) : (
                <div className="h-7"></div>
              )}
            </div>

            {/* Description */}
            <p className="text-sm text-neutral-600 line-clamp-2 leading-relaxed mb-3 h-10">
              {description ? description.substring(0, 150) : 'Discover comfort and authentic hospitality'}
            </p>

            {/* Badges */}
            <div className="mb-4 h-6 flex items-start overflow-hidden">
              {badges && badges.length > 0 && (
                <div className="flex flex-wrap gap-1.5">
                  {badges.slice(0, 3).map((badge, i) => (
                    <Badge key={i} type={badge} />
                  ))}
                </div>
              )}
            </div>

            {/* Price & CTA */}
            <div className="flex items-center justify-between pt-4 border-t border-neutral-100 mt-auto">
              <div className="min-w-0 flex-1">
                {price > 0 ? (
                  <div>
                    <div className="text-sm text-neutral-500 font-medium mb-0.5">From</div>
                    <div className="flex items-baseline gap-1">
                      <span className="text-2xl font-bold text-emerald-600">${price}</span>
                      <span className="text-xs text-neutral-500 font-medium">{priceLabel}</span>
                    </div>
                  </div>
                ) : (
                  <div className="text-sm font-semibold text-neutral-600">Price on request</div>
                )}
              </div>
              <Button
                variant="secondary"
                size="sm"
                className="px-4 py-2 bg-emerald-50 text-emerald-700 hover:bg-emerald-600 hover:text-white font-semibold rounded-lg transition-all duration-300 flex-shrink-0 shadow-sm"
              >
                View Deal
              </Button>
            </div>
          </div>
        </div>
      </Link>
    );
  }

  // Horizontal layout for list view
  return (
    <Link href={`/package/${id || 1}`} className="group block">
      <div className="bg-white rounded-2xl overflow-hidden shadow-[0_2px_8px_rgba(0,0,0,0.04)] hover:shadow-[0_8px_30px_rgba(0,0,0,0.12)] transition-all duration-500 border border-neutral-100 hover:border-emerald-200 flex flex-col sm:flex-row group-hover:-translate-y-1">
        {/* Image */}
        <div className="relative w-full sm:w-80 aspect-[4/3] flex-shrink-0 overflow-hidden bg-gradient-to-br from-neutral-100 to-neutral-50">
          <ImageWithFallback
            src={image}
            alt={title}
            className="absolute inset-0 w-full h-full object-cover group-hover:scale-110 transition-transform duration-700 ease-out"
          />
          <div className="absolute inset-0 bg-gradient-to-t from-black/5 via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-500" />

          <button
            onClick={(e) => {
              e.preventDefault();
              setIsWishlisted(!isWishlisted);
            }}
            className="absolute top-4 right-4 w-10 h-10 bg-white/90 backdrop-blur-md rounded-full flex items-center justify-center shadow-lg hover:bg-white hover:scale-110 transition-all duration-300 z-10"
          >
            <Heart className={`w-[18px] h-[18px] transition-all duration-300 ${isWishlisted ? 'fill-red-500 text-red-500 scale-110' : 'text-neutral-700'}`} />
          </button>
        </div>

        {/* Content */}
        <div className="p-6 flex flex-col gap-3 flex-1 min-w-0">
          {/* Title & Location */}
          <div>
            <h3 className="text-xl font-bold text-neutral-900 line-clamp-1 mb-2 group-hover:text-emerald-600 transition-colors duration-300">
              {title}
            </h3>
            <div className="flex items-center gap-2 text-neutral-600">
              <MapPin className="w-4 h-4 flex-shrink-0 text-emerald-500" />
              <span className="text-sm font-medium line-clamp-1">{location}</span>
            </div>
          </div>

          {/* Rating */}
          {hasValidRating && (
            <div className="flex items-center gap-2">
              <div className="flex items-center gap-1.5 px-3 py-1.5 bg-gradient-to-r from-yellow-50 to-amber-50 rounded-lg border border-yellow-100">
                <Star className="w-4 h-4 fill-yellow-400 text-yellow-400" />
                <span className="text-sm font-bold text-neutral-900">{formattedRating}</span>
              </div>
              {hasReviews && (
                <span className="text-sm text-neutral-500 font-medium">({reviews.toLocaleString()} reviews)</span>
              )}
            </div>
          )}

          {/* Description */}
          {description && (
            <p className="text-sm text-neutral-600 line-clamp-2 leading-relaxed">
              {description.replace(/<[^>]*>/g, '').substring(0, 200)}
            </p>
          )}

          {/* Badges */}
          {badges && badges.length > 0 && (
            <div className="flex flex-wrap gap-2">
              {badges.slice(0, 4).map((badge, i) => (
                <Badge key={i} type={badge} />
              ))}
            </div>
          )}

          {/* Price & CTA */}
          <div className="flex items-center justify-between pt-4 border-t border-neutral-100 mt-auto">
            <div>
              {price > 0 ? (
                <div>
                  <div className="text-sm text-neutral-500 font-medium mb-1">From</div>
                  <div className="flex items-baseline gap-1">
                    <span className="text-3xl font-bold text-emerald-600">${price}</span>
                    <span className="text-sm text-neutral-500 font-medium">{priceLabel}</span>
                  </div>
                </div>
              ) : (
                <div className="text-base font-semibold text-neutral-600">Price on request</div>
              )}
            </div>
            <Button
              variant="secondary"
              size="sm"
              className="px-6 py-2.5 bg-emerald-50 text-emerald-700 hover:bg-emerald-600 hover:text-white font-semibold rounded-lg transition-all duration-300 shadow-sm"
            >
              View Details
            </Button>
          </div>
        </div>
      </div>
    </Link>
  );
}
